/* center the actions horizontally in #quote-box */
.QuoteBoxActions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

/* size the components to have the same weight  */
.ShareQuote, .GetNewColor {
  background: none;
  border: none;
  /* margin to slightly distance the SVG from one another */
  margin: 0 1rem;
  /* transition for the hover, focus and active states */
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}
.ShareQuote svg, .GetNewColor svg {
  width: 2.75rem;
  height: 2.75rem;
  fill: var(--color-theme);
}
/* on hover and on focus increase the brightness of the svg */
.ShareQuote:hover, .GetNewColor:hover, .ShareQuote:focus, .GetNewColor:focus {
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
}
/* when active, increase the scale */
.ShareQuote:active, .GetNewColor:active {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}

/* for smaller viewports, resize the svg as to avoid excessive visual weight */
@media (max-width: 700px) {
  .ShareQuote svg, .GetNewColor svg {
    width: 2rem;
    height: 2rem;
  }
}


